.baner img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    margin-bottom: 30px;
}

.row {
    display: flex;
}

.img img {
    margin-right: 30px;
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.name {
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 20px;
}

.form {
    width: 100%;
    margin-top: 30px;
}

.area {
    width: 100%;
    height: 100px;
    resize: none;
    font-size: 22px;
    line-height: 25px;
    padding: 10px;
    margin-top: 10px;
    box-sizing: border-box;
}

.btn {
    cursor: pointer;
    display: block;
    padding: 10px;
    margin-left: auto;
    background-color: green;
    color: #fff;
}

.posts {
    width: 100%;
    margin: 50px 0px;
}

.post {
    display: flex;
    align-items: center;
}

.post:not(:last-child) {
    margin-bottom: 30px;
}

.postimg {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: green;
    margin-right: 30px;
}

.posttext {
    font-size: 22px;
    line-height: 26px;
}
